  import urls from '../../utils/urls.js'
  import wxInfoDialog from '../components/wxInfoDialog.vue'
  export default {
    components: {
      wxInfoDialog
    },
    data () {
      return {
        adv_img: '',
        link: '',
        urlHttp: '',
        advShow: false,
        time: 5,
        timeOut: {},
        interval: {},
        provider: '',
        showDialog: false
      }
    },
    onShow() {
			this.time=5
      this.urlHttp = urls.UrlHttp
      uni.request({
        url: urls.HTTP + '/index/advertisement',
        method: 'POST',
        success: (res) => {
          // console.log('广告', res)
          this.adv_img = res.data.advertisement.picpath
          this.link = res.data.advertisement.link
        }
      })
      this.interval = setInterval(() => {
        --this.time
      }, 1000)
      this.timeOut = setTimeout(() => {
        clearInterval(this.interval)
        switch(uni.getSystemInfoSync().platform) {
          case 'android':






						if (!uni.getStorageSync('openid')) {
							if (!uni.getStorageSync('user')){
								this.showDialog = true
							}else{
								uni.switchTab({
									url: '../find/find'
								})
							}
						} else if (!uni.getStorageSync('user').id) {
							uni.redirectTo({
								url: 'register'
							})
						} else {
							uni.switchTab({
								url: '../find/find'
							})
						}

          break;
          case 'ios':






						if (!uni.getStorageSync('openid')) {
							if (!uni.getStorageSync('user')){
								this.showDialog = true
							}else{
								uni.switchTab({
									url: '../find/find'
								})
							}
						} else if (!uni.getStorageSync('user').id) {
							uni.redirectTo({
								url: 'register'
							})
						} else {
							uni.switchTab({
								url: '../find/find'
							})
						}

          break;
          default:
					 if (!uni.getStorageSync('openid')) {
					   if (!uni.getStorageSync('user')){
					   	this.showDialog = true
					   }else{
					   	uni.switchTab({
					   	  url: '../find/find'
					   	})
					   }
					 } else if (!uni.getStorageSync('user').id) {
					   uni.redirectTo({
					     url: 'register'
					   })
					 } else {
					   uni.switchTab({
					     url: '../find/find'
					   })
					 }
          break;
        }
      }, 5000)
    },
    onUnload () {
      clearInterval(this.interval)
      clearTimeout(this.timeOut)
    },
    methods: {
      closeDialog: function (val) {
        this.showDialog = val
      },
      goNext: function () {
        switch(uni.getSystemInfoSync().platform) {
          case 'android':






          	if (!uni.getStorageSync('openid')) {
							if (!uni.getStorageSync('user')){
								this.showDialog = true
							}else{
								uni.switchTab({
									url: '../find/find'
								})
							}
						} else if (!uni.getStorageSync('user').id) {
							uni.redirectTo({
								url: 'register'
							})
						} else {
							uni.switchTab({
								url: '../find/find'
							})
						}

          break;
          case 'ios':






						if (!uni.getStorageSync('openid')) {
							if (!uni.getStorageSync('user')){
								this.showDialog = true
							}else{
								uni.switchTab({
									url: '../find/find'
								})
							}
						} else if (!uni.getStorageSync('user').id) {
							uni.redirectTo({
								url: 'register'
							})
						} else {
							uni.switchTab({
								url: '../find/find'
							})
						}

          break;
          default:
          if (!uni.getStorageSync('openid')) {
						if (!uni.getStorageSync('user')){
							this.showDialog = true
						}else{
							uni.switchTab({
							  url: '../find/find'
							})
						}
          } else if (!uni.getStorageSync('user').id) {
            uni.redirectTo({
              url: 'register'
            })
          } else {
            uni.switchTab({
              url: '../find/find'
            })
          }
          break;
        }
      },
      goAdv: function () {
        this.advShow = false
        clearInterval(this.interval)
        clearTimeout(this.timeOut)
				plus.runtime.openURL(this.link);  	
      }
    }
  }
